import axios from '../utils/request';
export const getList = (data: any): Promise<any> => {
  return axios.request({
    url: "/getList",
    method: "POST",
    data,
  });
};
export const getUserList = (params: any): Promise<any> => {
  return axios.request({
    url: "/getUserList",
    method: "GET",
    params,
  });
};
