import React, { Component } from 'react';
import './SetPass.less';
import * as API from '../../apis/hxc';
import { RouteComponentProps } from "react-router-dom";

import throttle from 'lodash/throttle';
import debounce from 'lodash/debounce';


import { EyeInvisibleOutlined, EyeOutlined } from '@ant-design/icons';
import { Button, message, Statistic, Row, Col } from "antd";
const { Countdown } = Statistic;

interface IProps {
  status?: any;
  guanbiWangPass?: any;
}
export default class SetPass extends Component<IProps> {
  debounce: any;
  constructor(porps: IProps | Readonly<IProps>) {
    super(porps);
    this.get_huoqu_code = throttle(this.get_huoqu_code, 2000);//获取验证码  节流
    // this.get_phone = debounce(this.get_phone, 2000);
  }
  state = {
    isPassword: true,//密码框的隐藏与显示的切换
    getPhone: '',//获取手机号
    getCode: '', //获取验证码
    getPassword: '',//获取密码框内容
    isClass: false,//手机号输入错误时 警告样式
    isBtn: false,//获取验证码 是否可点
    isDaoJiShi: false,//倒计时UI
  };
  render() {
    const { isPassword, getPhone, getCode, getPassword, isClass, isBtn, isDaoJiShi } = this.state;
    return (
      <div>
        <div className='SetPass_box'>
          <div className='content'>
            <div className='header'>
              <b>重置密码</b>
            </div>
            <div className='list'>
              <p>
                {/* onChange={(e) => { this.get_phone(e); }} */}
                <input type="text" className={isClass ? 'active' : ''} placeholder='手机号' onFocus={() => { this.phone_focus(); }} onBlur={() => { this.phone_blur(); }} defaultValue={getPhone} onChange={(e) => { this.get_phone(e); }} />
              </p>
              <p className='daojishi'>
                <input type="text" value={getCode} onChange={(e) => { this.get_code(e.target.value); }} placeholder='验证码' />
                <button disabled={isBtn ? true : false} className={isBtn ? 'list_p_span_s' : 'list_p_span'} onClick={() => { this.get_huoqu_code(); }}>
                  {
                    isDaoJiShi ? <Countdown title="" value={Date.now() + 59 * 1000} onChange={this.onChange} format="ss" /> : null
                  }
                  获取验证码
                </button>
              </p>
              <p>
                <input value={getPassword} onChange={(e) => { this.get_password(e.target.value); }} type={isPassword ? "password" : "text"} placeholder='新密码' />
                <span className='list_span' onClick={() => { this.yinOrXian(); }}>
                  {
                    isPassword ? <EyeInvisibleOutlined /> : <EyeOutlined />
                  }
                </span>
              </p>
            </div>
            <div>
              <div className="btn">
                <Button type="primary" size='large' onClick={() => { this.btn_queding(); }} >确定</Button>
                <Button type="primary" size='large' onClick={() => { this.props.guanbiWangPass(); }} >返回登录</Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }



  //倒计时onChange事件
  onChange = (val: any) => {
    if (val <= 100) {
      console.log('changed!');
      this.setState({ isBtn: false, isDaoJiShi: false });
    }
  };

  //获取手机号
  get_phone(e: React.ChangeEvent<HTMLInputElement>) {
    this.setState({ getPhone: e.target.value });
  }



  //手机输入框，失去焦点
  phone_blur() {
    const TELTYPE = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
    if (TELTYPE.test(this.state.getPhone) === false) {
      message.warning('手机号格式不正确', 3);
      this.setState({ isClass: true });
    }
  }

  //点击获取验证码
  get_huoqu_code() {
    if (this.state.getPhone === '') {
      message.warning('手机号不不能为空', 3);
      return;
    } else {
      this.setState({ isBtn: true, isDaoJiShi: true });
      const TELTYPE = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
      const phone = this.state.getPhone;
      const random = Math.random().toString(10).slice(2, 6);
      console.log(random);
      if (TELTYPE.test(this.state.getPhone)) {
        API.setPassCode({ phone, random }).then((res) => {
          console.log(res);
          if (res.masg == 1) {
            message.warning('手机号不存在', 3);
          }
        });
      }
    }
  }

  //手机输入框，获取焦点
  phone_focus() {
    this.setState({ isClass: false });
  }

  //获取验证码框中的value
  get_code(val: string) {
    this.setState({ getCode: val }, () => {
      // console.log(this.state.getCode);
    });
  }

  get_password(val: string) {
    this.setState({ getPassword: val }, () => {
      // console.log(this.state.getPassword);
    });
  }

  //密码框的隐藏与显示的切换
  yinOrXian() {
    this.setState({ isPassword: !this.state.isPassword });
  }

  //确定按钮 修改密码
  btn_queding() {
    const phone = this.state.getPhone;
    const codes = this.state.getCode;
    const password = this.state.getPassword;
    if(phone===''){
      message.warning('请输入手机号', 3);
      return;
    }else if(codes===''){
      message.warning('验证码不能为空', 3);
      return;
    }else if(password===''){
      message.warning('新密码不能为空', 3);
      return;
    }
    API.HXCxiuPass({ phone, codes, password }).then((res) => {
      if (res.masg == 2) {
        message.warning('验证码错误', 3);
      } else if (res.masg == 1) {
        message.warning('修改成功', 3);
        setTimeout(() => {
          this.props.guanbiWangPass();
        }, 1000);
      }
    });
  }
}
