import * as TYPE from "../action-types";

const action = {
  setOriginList(payload: object) {
    return {
      type: TYPE.SETORIGINLIST,
      payload,
    };
  },
  setStepStatus(payload: object) {
    return {
      type: TYPE.SETSTEPSTATUE,
      payload,
    };
  },
  setQuesFocus(payload: object) {
    return {
      type: TYPE.SETQUESFOCUS,
      payload,
    };
  },
  // setFocusMsg(payload: object) {
  //   return {
  //     type: TYPE.SETFOCUSMSG,
  //     payload,
  //   };
  // },
  setQuesType(payload: object) {
    return {
      type: TYPE.SETQUESTYPE,
      payload,
    };
  },
  setExamId(payload: object) {
    return {
      type: TYPE.SETEXAMID,
      payload,
    };
  },
  updateQuesList(payload: object) {
    return {
      type: TYPE.UPDATEQUESLIST,
      payload,
    };
  },
  setFocusMsg(payload: object) {
    return {
      type: TYPE.SETFOCUSMSG,
      payload,
    };
  },
  changeShowSerial(payload: object) {
    return {
      type: TYPE.CHANGESHOWSERIAL,
      payload,
    };
  },
  setHiddenHolder(payload: object) {
    return {
      type: TYPE.SETHIDDENHOLDER,
      payload,
    };
  },
  setLoginId(payload: object) {
    return {
      type: TYPE.SETLOGINID,
      payload,
    };
  },
  setTypeList(payload: object) {
    return {
      type: TYPE.SETTYPELIST,
      payload,
    };
  },
  setQuestionStore(payload: object) {
    return {
      type: TYPE.SETQUESTIONSTORE,
      payload,
    };
  },
  setExamStatus(payload: object) {
    return {
      type: TYPE.SETEXAMSTATUS,
      payload,
    };
  },
  scrollToBottom() {
    document.documentElement.scrollTo({
      behavior: "smooth",
      top: document.documentElement.scrollHeight,
    });
    return {
      type: "",
      payload: {},
    };
  },
  scrollToActiveQues(index: number) {
    const top = document
      .querySelector(
        `.normal-question-list .question-item:nth-child(${index + 1})`
      )
      ?.getBoundingClientRect().top;
    // console.log(index, top);
    document.documentElement.scrollTo({
      behavior: "smooth",
      top: top ? top - 75 + document.documentElement.scrollTop : 0,
    });
    return {
      type: "",
      payload: {},
    };
  },
};

export default action;
