import React, { Component } from "react";

import { RouteComponentProps } from "react-router-dom";
import * as API from '../../apis/hxc';
import imgs from '../../img/bei1.jpeg';
import './login.less';
import { EyeInvisibleOutlined, EyeOutlined } from '@ant-design/icons';

import { Button, message, Statistic, Row, Col } from "antd";
const { Countdown } = Statistic;
import throttle from 'lodash/throttle';//插件节流

import SetPass from './SetPass';
import { connect } from "react-redux";
import action from "../../store/actions";
interface IProps extends RouteComponentProps {
  status?: any;
  guanbiWangPass?: any;
  setLoginId: (payload:{ id: number }) => void;
}
interface IState {
  username?: string,
  tabIndex: number,//密码登录和验证码登录切换
  user_phone: string,//用户名或手机号,
  password: string,//密码
  codes: string,//验证码
  isSed: boolean,//手机号输入错误，变为红色（改变样式）
  masg: string,//提示信息
  isPass: boolean,//密码输入框显示与隐藏切换
  isSetPass: boolean,//跳转忘记密码页面

  isBtn: boolean,//获取验证码 是否可点
  isDaoJiShi: boolean,//倒计时UI的显示/隐藏
}
class Login extends Component<IProps, IState> {
  constructor(props: IProps | Readonly<IProps>) {
    super(props);
    this.getCode = throttle(this.getCode, 2000);//获取验证码  节流
  }
  state = {
    tabIndex: 0,
    user_phone: '',
    password: '',
    codes: '',
    isSed: false,
    masg: '',
    isPass: true,
    isSetPass: false,

    isDaoJiShi: false,
    isBtn: false,
  };
  render() {
    const { tabIndex, user_phone, password, codes, isSed, isPass, isSetPass, isDaoJiShi, isBtn } = this.state;
    return (
      <div>
        <div className="loginImg">
          <div>Login</div>
          {/* <Button
            onClick={() => {
              this.props.history.push("/layout");
            }}
          >去首页</Button> */}
          <div className="con">
            <div className="header">
              <p className={tabIndex === 0 ? 'sed' : ''} onClick={() => { this.setState({ tabIndex: 0, user_phone: '', password: '', isSed: false }); }}>密码登录</p>
              <p className={tabIndex === 1 ? 'sed' : ''} onClick={() => { this.setState({ tabIndex: 1, user_phone: '', codes: '', isSed: false }); }}>验证码登录</p>
            </div>
            <div>
              {
                tabIndex === 0 ? <div className="inp">
                  <p><input type="text" placeholder="手机号/用户名" className={isSed ? "active" : ""} value={user_phone} onChange={(e) => { this.getUser_phong(e.target.value); }} onBlur={() => { this.blurUser_phone(); }} onFocus={() => { this.focusUser_phone(); }} /></p>
                  <p><input type={isPass ? 'password' : 'text'} placeholder="密码" value={password} onChange={(e) => { this.getUser_password(e.target.value); }} />
                    <span onClick={() => { this.tabIcon(); }}>
                      {
                        isPass ? <EyeInvisibleOutlined /> :
                          <EyeOutlined />
                      }
                    </span>
                  </p>
                  <p className="wangPss" onClick={() => { this.wangPass(); }}>忘记密码</p>
                  <div className="btn"><Button type="primary" size='large' onClick={() => { this.btn_Ok(); }} >登录</Button></div>
                </div> : <div className="inp">
                  <p><input className={isSed === true ? "active" : ""} type="text" placeholder="手机号" value={user_phone} onChange={(e) => { this.getUser_phong(e.target.value); }} onBlur={() => { this.inpBlur(); }} onFocus={() => { this.inpFocus(); }} /></p>
                  <p className='daojishi'>
                    <input type="text" placeholder="验证码" value={codes} onChange={(e) => { this.getUser_codes(e.target.value); }} />
                    <button disabled={isBtn?true : false} className={isBtn?"code_span_s":"code_span"} onClick={() => { this.getCode(); }}>
                      {
                        isDaoJiShi ? <Countdown title="" value={Date.now() + 50 * 1000} onChange={this.onChange} format="ss" /> : null
                      }
                      获取验证码
                    </button>
                  </p>
                  <p className="wangPss" onClick={() => { this.wangPass(); }}>忘记密码</p>
                  <div className="btn"><Button type="primary" size='large' onClick={() => { this.btn_code(); }} >登录</Button></div>

                </div>
              }
            </div>

          </div>
          {
            isSetPass ? <SetPass guanbiWangPass={this.guanbiWangPass}></SetPass> : null
            // guanbiWangPass={this.guanbiWangPass}
          }
        </div>

      </div>
    );
  }
  componentDidMount() {
    // API.getUserList({}).then((res) => {//获取数据
    //   console.log(res);
    // });
  }



  //忘记密码
  wangPass() {
    this.setState({ isSetPass: true });
  }

  //子传父，关闭修改密码页面。
  guanbiWangPass = () => {
    this.setState({ isSetPass: false });
  };

  //password 显示与隐藏
  tabIcon() {
    this.setState({ isPass: !this.state.isPass });
  }


  //手机号/用户名 密码登录  失去焦点
  blurUser_phone() {
    if (this.state.user_phone.length < 6) {
      message.warning('手机号/用户名至少6位', 3);
      this.setState({ isSed: true });
    }
  }
  //手机号/用户名 密码登录  获取焦点
  focusUser_phone() {
    this.setState({ isSed: false });
  }


  //获取手机号
  getUser_phong(val: string) {
    this.setState({ user_phone: val }, () => {

    });
  }

  //获取密码
  getUser_password(val: string) {
    this.setState({ password: val }, () => {

    });
  }
  //获取验证码
  getUser_codes(val: string) {
    this.setState({ codes: val }, () => {

    });
  }

  //失去焦点 --手机验证码登录 
  inpBlur() {
    // console.log('失去焦点了');
    const TELTYPE = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
    if (TELTYPE.test(this.state.user_phone) === false) {
      message.warning('手机号格式不正确', 3);
      this.setState({ isSed: true });
    }
  }

  //获取焦点 --手机验证码登录 
  inpFocus() {
    this.setState({ isSed: false });//把警告样式去掉
  }

  //用户名或者手机号登录
  btn_Ok() {
    interface Iobj {
      usr_or_phone: string,
      password: string,
    }
    const obj: Iobj = {
      usr_or_phone: '',
      password: '',
    };
    obj.usr_or_phone = this.state.user_phone;
    obj.password = this.state.password;
    if (this.state.user_phone === '') {
      message.warning('手机号/用户名不能为空', 3);
      return;
    } else if (this.state.password === '') {
      message.warning('密码不能为空', 3);
      return;
    }
    if (this.state.isSed === true) {
      return;
    }
    API.HXCsetObj(obj).then((res) => {
      console.log(res.code);
      if (res.masg === 1) {
        message.success('登陆成功', 3);
        this.props.setLoginId({id: +res.user.id});
        this.props.history.push('/layout/home');
        localStorage.setItem('token', JSON.stringify(res.user));
      } else if (res.masg === 2) {
        message.warning('该账号已经停用', 3);
      } else if (res.masg === 3) {
        message.warning('密码错误', 3);
      } else if (res.masg === 4) {
        message.warning('账号不存在', 3);
      }
    });
  }

  //倒计时onChange事件
  onChange = (val: any) => {
    if (val <= 100) {
      console.log('changed!');
      this.setState({ isBtn: false, isDaoJiShi: false });
    }
  };

  //获取验证码
  getCode() {
    interface Iobj {
      usr_or_phone: string,
      codes: string,
    }
    const obj: Iobj = {
      usr_or_phone: '',
      codes: ''
    };
    const random = Math.random().toString(10).slice(2, 6);
    console.log(random);
    obj.usr_or_phone = this.state.user_phone;
    obj.codes = random;
    if (this.state.user_phone === '') {
      message.warning('手机号不能为空', 3);
      return;
    }
    this.setState({ isBtn: true, isDaoJiShi: true });
    API.HXCGetRandom(obj).then((res) => {
      console.log(res.code);
    });
  }

  //手机号--验证码登录
  btn_code() {
    interface Iobj {
      usr_or_phone: string,
      codes: string,
    }
    const obj: Iobj = {
      usr_or_phone: '',
      codes: ''
    };
    obj.usr_or_phone = this.state.user_phone;
    obj.codes = this.state.codes;
    // console.log(obj);
    if (this.state.user_phone === '') {
      message.warning('手机号不能为空', 3);
      return;
    } else {
      API.HXCGetCodes(obj).then((res)=>{
        if(res.masg==4){
          message.warning('手机号不存在', 3);
        }else if(res.masg==3){
          message.warning('验证码错误', 3);
        }else if(res.masg==1){
          message.warning('登陆成功', 3);
          localStorage.setItem('token', JSON.stringify(res.user));
          this.props.history.push('/layout/home');
        }
      });
    }
  }
}

interface IMapStateToProps {
  examEdit: object;
}
export default connect(
  (state: IMapStateToProps) => state.examEdit,
  action.examEdit
)(Login);